module.exports = {
  random: function(options) {
    var numberOfEmojis = options.count;
    var result = []
    for (i = 0, len = numberOfEmojis; i < len; i++) {
      randomEmoji = emojis[Math.floor(Math.random() * emojis.length)];
      result.push(randomEmoji);
    }
    return result;
  }
};



